import AsyncStorage from '@react-native-async-storage/async-storage';
import Dashboard from '../../../src/screens/Dashboard';
import { ThemeContext } from '../../../src/contexts/ThemeContext';
import { useProgress } from '../../../src/contexts/ProgressContext';
import { useCurrentLevel } from '../../../src/contexts/CurrentLevelContext';

// Mock des d√©pendances
jest.mock('@react-native-async-storage/async-storage', () => ({
  getItem: jest.fn(),
  setItem: jest.fn(),
}));

jest.mock('expo-linear-gradient', () => ({
  LinearGradient: ({ children, ...props }) => {
    const { View } = require('react-native');
    return <View {...props}>{children}</View>;
  }
}));

jest.mock('expo-router', () => ({
  router: {
    push: jest.fn(),
  },
}));

// Mock des contextes
jest.mock('../../../src/contexts/ProgressContext');
jest.mock('../../../src/contexts/CurrentLevelContext');

// Variables pour contr√¥ler les mocks dynamiquement
let mockLastActivity = {
  type: 'vocabulary',
  level: '2',
  title: 'Vocabulaire Famille',
  metadata: { word: 8, totalWords: 20, categoryIndex: 2 }
};
let mockIsLoading = false;
const mockGetLevelProgress = jest.fn((level) => {
  const progressMap = { '1': 25, '2': 50, '3': 75, '4': 30, '5': 10, '6': 0, 'bonus': 5 };
  return progressMap[level] || 0;
});

// Mock des hooks
jest.mock('../../../src/hooks/useRealTimeProgress', () => ({
  __esModule: true,
  default: () => ({
    getLevelProgress: mockGetLevelProgress,
    refresh: jest.fn().mockResolvedValue()
  })
}));

jest.mock('../../../src/hooks/useLastActivity', () => ({
  __esModule: true,
  default: () => ({
    lastActivity: mockLastActivity,
    isLoading: mockIsLoading,
    reload: jest.fn().mockResolvedValue()
  })
}));

// Mock des composants
jest.mock('../../../src/screens/Dashboard/components/HeroContinueSection', () => {
  const { View, Text, TouchableOpacity } = require('react-native');
  return ({ lastActivity, onPress }) => {
    if (lastActivity) {
      return (
        <TouchableOpacity onPress={() => onPress(lastActivity)}>
          <Text>Continuer ‚ñ∂Ô∏è</Text>
          <Text>{lastActivity.title}</Text>
        </TouchableOpacity>
      );
    }
    return (
      <TouchableOpacity onPress={() => onPress('levelSelection')}>
        <Text>Commencer &gt;</Text>
      </TouchableOpacity>
    );
  };
});

jest.mock('../../../src/utils/constants', () => ({
  LANGUAGE_LEVELS: { '1': { title: 'D√©butant', color: '#10B981', icon: 'üå±' }, '2': { title: '√âl√©mentaire', color: '#3B82F6', icon: 'üìö' }, '3': { title: 'Interm√©diaire', color: '#8B5CF6', icon: 'üéØ' }, '4': { title: 'Interm√©diaire+', color: '#F59E0B', icon: 'üöÄ' }, '5': { title: 'Avanc√©', color: '#EF4444', icon: '‚≠ê' }, '6': { title: 'Expert', color: '#EC4899', icon: 'üëë' }, 'bonus': { title: 'Bonus', color: '#6366F1', icon: 'üéÅ' } },
  EXERCISES: { vocabulary: { id: 'vocabulary', route: '/tabs/vocabulary', title: 'Vocabulaire', icon: 'üìö', color: '#3B82F6' }, grammar: { id: 'grammar', route: '/tabs/grammar', title: 'Grammaire', icon: 'üìù', color: '#8B5CF6' }, assessment: { id: 'assessment', route: '/tabs/assessment', title: 'Test', icon: 'üéØ', color: '#EF4444' } },
  CONTAINER_SAFE_EDGES: { NO_BOTTOM: ['top', 'left', 'right'] }
}));

jest.mock('../../../src/components/layout/Container', () => {
  const { ScrollView, View, RefreshControl } = require('react-native');
  const Container = ({ children, withScrollView = true, ...props }) => {
    if (withScrollView === false) return <View {...props}>{children}</View>;
    return <ScrollView {...props} refreshControl={<RefreshControl refreshing={false} onRefresh={() => {}} />}>{children}</ScrollView>;
  };
  Container.CONTAINER_SAFE_EDGES = { NO_BOTTOM: ['top', 'left', 'right'] };
  return Container;
});

jest.mock('../../../src/screens/VocabularyRevision/RevisionOrchestrator', () => {
  const { View } = require('react-native');
  return () => <View testID="revision-orchestrator" />;
});

const { router } = require('expo-router');

describe('Dashboard - Tests d\'int√©gration', () => {
  const mockThemeContext = { colors: { background: '#F8FAFC', primary: '#3B82F6', surface: '#FFFFFF', text: '#1F2937', textSecondary: '#6B7280' } };
  const mockProgressContext = { progress: { level1: 50, level2: 30, currentLevel: '1' }, isLoading: false, calculateGlobalProgress: jest.fn(() => 65), calculateLevelProgress: jest.fn((level) => ({ '1': 25, '2': 50, '3': 75 }[level] || 0)) };
  const mockCurrentLevelContext = { setCurrentLevel: jest.fn() };

  beforeEach(() => {
    jest.clearAllMocks();
    mockLastActivity = { type: 'vocabulary', level: '2', title: 'Vocabulaire Famille', metadata: { word: 8, totalWords: 20, categoryIndex: 2 } };
    mockIsLoading = false;
    useProgress.mockReturnValue(mockProgressContext);
    useCurrentLevel.mockReturnValue(mockCurrentLevelContext);
    AsyncStorage.getItem.mockResolvedValue(null);
    AsyncStorage.setItem.mockResolvedValue();
    router.push.mockClear();
    mockGetLevelProgress.mockClear();
    mockCurrentLevelContext.setCurrentLevel.mockClear();
  });

  const renderDashboard = () => render(<ThemeContext.Provider value={mockThemeContext}><Dashboard /></ThemeContext.Provider>);

  describe('Flux complet de navigation', () => {
    it('devrait permettre de continuer une activit√© existante', async () => {
      const { getByText } = renderDashboard();
      await waitFor(() => expect(getByText('Vocabulaire Famille')).toBeTruthy());
      fireEvent.press(getByText('Continuer ‚ñ∂Ô∏è'));
      expect(router.push).toHaveBeenCalledWith({ pathname: '/tabs/vocabulary', params: { level: '2' } });
    });

    it('devrait permettre de d√©marrer un nouvel exercice via QuickActions', async () => {
      const { getByText } = renderDashboard();
      await waitFor(() => expect(getByText('Vocabulaire')).toBeTruthy());
      fireEvent.press(getByText('Vocabulaire'));
      expect(router.push).toHaveBeenCalledWith({ pathname: '/tabs/vocabulary', params: { level: '1', mode: 'classic' } });
    });

    it('devrait permettre de changer de niveau et naviguer', async () => {
      const { findByText } = renderDashboard();
      
      const level2Circle = await findByText('2');
      fireEvent.press(level2Circle);

      const exploreButton = await findByText('Explorer le niveau 2');
      fireEvent.press(exploreButton);
      
      expect(mockCurrentLevelContext.setCurrentLevel).toHaveBeenCalledWith('2');
      expect(router.push).toHaveBeenCalledWith({ "pathname": "/tabs/exerciseSelection", "params": { "level": "2" } });
    });
  });

  describe('Gestion des erreurs et cas limites', () => {
    it('devrait g√©rer l\'absence d\'activit√© r√©cente', async () => {
      mockLastActivity = null;
      const { findByText } = renderDashboard();
      
      const startButton = await findByText('Commencer >');
      fireEvent.press(startButton);

      await waitFor(() => {
        expect(router.push).toHaveBeenCalledWith('/(tabs)/levelSelection');
      });
    });
  });

  describe('Performance et optimisation', () => {
    it('ne devrait pas recalculer inutilement les donn√©es', async () => {
      renderDashboard();
      await waitFor(() => {
        expect(mockGetLevelProgress).toHaveBeenCalled();
      });
    });
  });
});